/**
 *    fallback 
 */
function getFallbackPlanetMessages(): PersonalizedPlanetMessages {
  const planets = ['', '', '', '', '', '', '', '', '', ''];

  return {
    messages: planets.map(planet => ({
      planet,
      messages: getFallbackMessages(planet),
    })),
    timestamp: Date.now(),
  };
}
