export interface PeriodModalContent {
  question: string;
  joke: {
    emoji: string;
    text: string;
  };
}

export interface GeneratePeriodContentOptions {
  userName?: string;
  cycleStartISODate: string;
  cycleTimingContext?: string;
  signal?: AbortSignal;
  apiKey?: string;
  claudeProxyUrl?: string;
  openAIApiKey?: string;
}

export async function generatePeriodModalContent({
  userName,
  cycleStartISODate,
  cycleTimingContext,
  signal,
  apiKey,
  claudeProxyUrl,
  openAIApiKey,
}: GeneratePeriodContentOptions): Promise<PeriodModalContent> {
  const effectiveUserName = (userName && userName.trim()) ? userName.trim() : '';

  const cycleDate = new Date(cycleStartISODate);
  const readableDate = Number.isNaN(cycleDate.getTime())
    ? null
    : cycleDate.toLocaleDateString('ru-RU', {
        day: 'numeric',
        month: 'long',
        year: 'numeric',
      });

  const dateContext = readableDate
    ? `  : ${readableDate}.`
    : '';

  const cycleContextBlock = cycleTimingContext
    ? `   (   ):\n${cycleTimingContext}\n`
    : '         ,  ,       .\n';

  const instructions = `  -:    ,       .
  ${effectiveUserName},  - ,   .
   , ,      .    ,    .
  : (1)   ( 24 ),          ;   , PMS   .   . (2)     ,    - ,    ,      .    /,     .
${dateContext}
${cycleContextBlock}  ,    ,    :           / ;          .
       (1 ,  2,   ).      .

    JSON :
{
  "question": "   ",
  "joke": {
    "emoji": "1-2 ",
    "text": "  "
  }
}`;

  const { callAI } = await import('./aiClient');

  const result = await callAI({
    system: ' ""   ,      ,  .      JSON   .',
    messages: [
      {
        role: 'user',
        content: instructions,
      },
    ],
    temperature: 0.9,
    maxTokens: 500,
    signal,
    claudeApiKey: apiKey,
    claudeProxyUrl,
    openAIApiKey,
  });

  console.log(`Generated period modal content using ${result.provider}`);

  let parsed: PeriodModalContent;
  try {
    // AI   JSON  markdown ,  
    const cleanContent = result.text.replace(/```json\n?/g, '').replace(/```\n?/g, '').trim();
    parsed = JSON.parse(cleanContent);
  } catch (error) {
    throw new Error('Failed to parse AI response.');
  }

  return parsed;
}

export function getFallbackPeriodContent(userName = ''): PeriodModalContent {
  return {
    question: `, ${userName}!  ,   ,       ?`,
    joke: {
      emoji: '',
      text: ' :             .  ? ',
    },
  };
}
